import React, { Component } from 'react';
import { BrickController } from './brickController';
import { debounce } from 'lodash-es';
import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
`;

class Brick extends Component {
  
  constructor(props) {
    super(props);
    this.worldContainerRef = React.createRef();
    this.canvasRef = React.createRef();
    this.controller = null;
  }

  componentDidMount() {
    this.controller = new BrickController(this.canvasRef.current);
    this.resetSize();
    this.controller.loop();
    window.addEventListener('resize', this.debounceResetSize);
  }

  resetSize = () => {
    this.worldContainerRef.current.style.width = window.innerWidth + 'px';
    this.worldContainerRef.current.style.height = window.innerHeight + 'px';
  }

  debounceResetSize = debounce(this.resetSize, 200, {
    leading: true,
  });

  componentWillUnmount() {
    window.removeEventListener('resize', this.debounceResetSize);
    
    if (this.controller) {
      this.controller.dispose();
      this.controller = null;
    }
  }

  render() {
    return (
      <Container
        ref={this.worldContainerRef}
        className="brick-world"
      >
        <canvas ref={this.canvasRef} />
      </Container>
    );
  }

}

export default Brick;
