import { Bodies } from 'matter-js';

export class ManOutline {

  static Width = 156;
  static Height = 156;

  constructor(x, y) {
    this.__x = x;
    this.__y = y;

    this.body = Bodies.fromVertices(
      x, y,
      [
        { x: 31.5, y: 34.5 },
        { x: 81.5, y: 88 },
        { x: 126.5, y: 47.5 },
        { x: 97.5, y: 103.5 },
        { x: 56.5, y: 103.5 }
      ],
      { isStatic: true },
    )

    this.__img = new Image();
    this.__img.src = '/images/MAN.png';
  }

  /**
   * 
   * @param {CanvasRenderingContext2D} ctx 
   */
  draw(ctx) {
    ctx.save(0);
    ctx.translate(this.__x, this.__y);
    ctx.drawImage(
      this.__img,
      -1 * ManOutline.Width / 2 + 5,
      -1 * ManOutline.Height / 2 - 10 ,
      ManOutline.Width,
      ManOutline.Height,
    );
    ctx.restore();
  }

  dispose() {

  }

}
