import { Bodies } from 'matter-js';

export class Ground {

  /**
   * 
   * @param {number} x 
   * @param {number} y 
   * @param {number} width
   * @param {number} height
   */
  constructor(x, y, width, height) {
    this.__width = width;
    this.__height = height;
    this.body = Bodies.rectangle(
      x, y,
      width, height,
      {
        isStatic: true,
        friction: 0.99,
        frictionStatic: 0.99,
      },
    );
  }

  /**
   * 
   * @param {CanvasRenderingContext2D} ctx 
   */
  draw(ctx) {
    const { position: { x, y } } = this.body;
    ctx.save();
    ctx.fillStyle = "black";
    ctx.translate(x, y);
    ctx.fillRect(-0.5 * this.__width, -this.__height * 0.5, this.__width, this.__height);
    ctx.restore();
  }

  dispose() {

  }

}
