import { Engine, World, Mouse, MouseConstraint, Events } from 'matter-js';
import { BrickAtom } from './brickAtom';
import { Ground } from './ground';
import { LargeBrick } from './largeBrick';
import { ManOutline } from './manOutline';

export class BrickController {

  static CanvasWidth = 920;
  static CanvasHeight = 480;

  /**
   * @param {HTMLCanvasElement} dom 
   */
  constructor(dom) {
    this.__dom = dom;
    dom.width = BrickController.CanvasWidth;
    dom.height = BrickController.CanvasHeight;
    // this.setCanvasRatio(dom);
    this.__world = World.create({
      bounds: {
        min: {
          x: 0, y: 0,
        },
        max: {
          x: BrickController.CanvasWidth,
          y: BrickController.CanvasHeigh,
        },
      }
    });

    this.__engine = Engine.create({
      world: this.__world,
    });

    // this.__render = Render.create({
    //   canvas: this.__dom,
    //   engine: this.__engine,
    //   options: {
    //     width: BrickController.CanvasWidth,
    //     height: BrickController.CanvasHeight,
    //     // showAxes: true,
    //     // showAngleIndicator:true
    //   },
    // });

    this.__mouse = Mouse.create(this.__dom);
    this.__mouseConstraint = MouseConstraint.create(this.__engine, {
      mouse: this.__mouse,
      constraint: {
        stiffness: 0.2,
        render: {
            visible: false,
        },
      },
    });

    World.add(this.__engine.world, this.__mouseConstraint);

    this.__engine.world.gravity.y = 1;

    this.__bricks = [];
    this.__ground = null;
    this.__lastTimeStamp = null;
    this.createGround();
    this.createBodies();
    this.resetLargeBrick();
  }

  /**
   * 
   * @param {HTMLCanvasElement} dom 
   */
  setCanvasRatio(canvas) {
    const context = canvas.getContext('2d');
    var devicePixelRatio = window.devicePixelRatio || 1
    const backingStoreRatio = context.webkitBackingStorePixelRatio ||
                            context.mozBackingStorePixelRatio ||
                            context.msBackingStorePixelRatio ||
                            context.oBackingStorePixelRatio ||
                            context.backingStorePixelRatio || 1
    const ratio = devicePixelRatio / backingStoreRatio;

    const oldWidth = canvas.width;
    const oldHeight = canvas.height;
    canvas.width = oldWidth * ratio;
    canvas.height = oldHeight * ratio;
    canvas.style.width = oldWidth + 'px';
    canvas.style.height = oldHeight + 'px';
    context.scale(this.__ratio * 2, this.__ratio * 2);
    this.__ratio = ratio;
  }

  createGround() {
    this.__ground = new Ground(
      480, 420,
      BrickController.CanvasWidth, 10,
    );
    this.__bricks.push(this.__ground);
    // World.add(this.__engine.world, this.__ground.body);
  }

  createBodies() {
    const beginX = 620;
    const beginY = 400;

    for (let i = 0; i < 4; i++) {
      const count = 5 - i;
      for (let j = 0; j < count; j++) {
        const atom = new BrickAtom(beginX + BrickAtom.width * (j + i/2), beginY - BrickAtom.height * i);
        this.__bricks.push(atom);
      }
    }

    const manOutline = new ManOutline(110, 348);
    this.__bricks.push(manOutline);

    this.__bricks.forEach(brick => {
      World.add(this.__engine.world, brick.body);
    });
  }

  loop() {
    // window.requestAnimationFrame(this.draw);
    // Events.on(this.__render, "afterRender", this.handleAfterRender);
    Events.on(this.__engine, "afterUpdate", this.handleAfterUpdate);
    Events.on(this.__mouseConstraint, "mousemove", this.handleMouseMove);
    Events.on(this.__mouseConstraint, "mouseup", this.handleMouseUp);
    Events.on(this.__mouseConstraint, "startdrag", this.handleDragStart);
    Events.on(this.__mouseConstraint, "enddrag", this.handleDragEnd);
    Engine.run(this.__engine);
    // Render.run(this.__render);
  }

  handleMouseMove = e => {
    if (this.__largeBrick && this.__largeBrick.isDragging) {
      const { position: { x, y } } = e.mouse;
      if (x > LargeBrick.EndX - 120) {
        const newBrick = new BrickAtom(x, y);
        this.__bricks.push(newBrick);
        this.__tmpBrick = newBrick;

        this.resetLargeBrick();
      }
    }

    if (this.__tmpBrick) {
      const { position } = e.mouse;
      this.__tmpBrick = position;
    }
  }

  handleMouseUp = e => {
    console.log(e);
    if (this.__tmpBrick) {
      World.add(this.__engine.world, this.__tmpBrick.body);
    }
  }

  handleAfterUpdate = () => {
    this.__largeBrick.update();

    /** @type {CanvasRenderingContext2D} */
    const ctx = this.__dom.getContext('2d');

    ctx.clearRect(0, 0, BrickAtom.width, BrickAtom.height);
    ctx.fillStyle = "#FFF3DE";
    ctx.fillRect(0, 0, BrickController.CanvasWidth, BrickController.CanvasHeight);
    // this.__ground.draw(ctx);
    this.__bricks.forEach(brick => brick.draw(ctx));
  }

  handleDragStart = e => {
    if (this.__largeBrick && e.body === this.__largeBrick.body) {
      this.__largeBrick.isDragging = true;
    }
  }

  handleDragEnd = e => {
    if (this.__largeBrick && e.body === this.__largeBrick.body) {
      this.resetLargeBrick();
    }
  }

  resetLargeBrick() {
    if (this.__largeBrick) {
      this.__largeBrick.dispose();
      World.remove(this.__engine.world, this.__largeBrick.body);
      this.__bricks = this.__bricks.filter(brick => brick !== this.__largeBrick);
    }

    this.__largeBrick = new LargeBrick(120, 30);
    World.add(this.__engine.world, this.__largeBrick.body);
    this.__bricks = [this.__largeBrick].concat(this.__bricks);
  }

  // draw = timestamp => {
  //   if (!this.__lastTimeStamp) {
  //     this.__lastTimeStamp = timestamp;
  //   }
  //   const delta = timestamp - this.__lastTimeStamp;
  //   this.__lastTimeStamp = timestamp;

  //   // do something
  //   Engine.update(this.__engine, delta);

  //   const ctx = this.__dom.getContext('2d');
  //   ctx.clearRect(0, 0, BrickAtom.width, BrickAtom.height);
  //   this.__bricks.forEach(brick => brick.draw(ctx));

  //   window.requestAnimationFrame(this.draw)
  // }

  dispose() {
    // Render.stop(this.__render);
    Engine.clear(this.__engine);
    this.__bricks.forEach(brick => {
      try {
        brick.dispose();
      } catch (err) {
        console.error(err);
      }
    });
    // window.cancelAnimationFrame(this.draw);
  }

}
