import { Bodies, Body } from 'matter-js';

export class BrickAtom {

  static width = 42;
  static height = 24;

  /**
   * @param {number} x 
   * @param {number} y 
   */
  constructor(x, y) {
    this.body = Bodies.rectangle(
      x,
      y,
      BrickAtom.width,
      BrickAtom.height,
      {
        density: 10,
        frictionAir: 0.035,
        friction: 0.1,
      },
    );
    this.__img = new Image();
    this.__img.src = '/images/brick.png';
  }

  get position() {
    return this.body.position;
  }

  set position({ x, y }) {
    Body.set(this.body, {
      position: { x, y },
    });
  }

  /**
   * 
   * @param {CanvasRenderingContext2D} ctx 
   */
  draw(ctx) {
    const { position, angle } = this.body;
    ctx.save();
    ctx.translate(position.x, position.y);
    ctx.rotate(angle);
    ctx.drawImage(
      this.__img,
      -1 * BrickAtom.width / 2,
      -1 * BrickAtom.height / 2,
      BrickAtom.width,
      BrickAtom.height,
    );
    ctx.restore();
  }

  dispose() {
  }

}
