import { Bodies, Body } from 'matter-js';
import decomp from 'poly-decomp';

if (typeof window !== 'undefined') {
  window.decomp = decomp;
}

export class LargeBrick {

  static Width = 512;
  static Height = 512;
  static EndX = 680;

  /**
   * 
   * @param {number} x 
   * @param {number} y 
   * @param {number} scale
   */
  constructor(x, y, scale = 0.5) {
    this.body = Bodies.fromVertices(
      x,
      y,
      [
        { x: 306, y: 8.5 },
        { x: 500, y: 75 },
        { x: 505.5, y: 162.5 },
        { x: 265.5, y: 500.5 },
        { x: 14, y: 304 },
        { x: 14, y: 240 },
        { x: 306, y: 8.5 },
      ],
      {
        density: 200,
        frictionAir: 0.035,
        friction: 0.78,
      },
    );
    this.__originalScale = scale;
    this.scale = scale;
    this.__img = new Image();
    this.__img.src = '/images/large-brick-rect.png';
    this.isDragging = false;
  }

  get scale() {
    return this.__scale;
  }

  set scale(value) {
    this.__scale = value;
    Body.scale(this.body, value, value);
  }

  update() {
    const { position: { x } } = this.body;
    if (x >= 200 && x < LargeBrick.EndX) {
      const ratio = (LargeBrick.EndX - x) / (LargeBrick.EndX - 200);
      // this.scale = this.__originalScale * ratio;
      this.__scale = this.__originalScale * ratio;
      // Body.scale(this.body, ratio / this.__originalScale, ratio / this.__originalScale);
      // this.__originalScale = this.__originalScale * ratio;
    } else {
      // this.scale = this.__originalScale;
    }
      // this.scale = this.__originalScale;
  }

  /**
   * 
   * @param {CanvasRenderingContext2D} ctx 
   */
  draw(ctx) {
    const { position, angle } = this.body;
    ctx.save();
    ctx.translate(position.x, position.y);
    ctx.rotate(angle);
    ctx.drawImage(
      this.__img,
      // 0, 0,
      -1 * LargeBrick.Width / 2 * this.__scale,
      -1 * LargeBrick.Height / 2 * this.__scale,
      LargeBrick.Width * this.__scale,
      LargeBrick.Height * this.__scale,
    );
    ctx.fillStyle ="black";
    ctx.fillText(
      `x: ${position.x} y: ${position.y}`,
      -1 * LargeBrick.Width / 2 * this.__scale,
      -1 * LargeBrick.Height / 2 * this.__scale,
    )
    ctx.restore();
  }

  dispose() {

  }

}
